.Home {
    background-color: #e3e6e0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    font-size: calc(10px + 2vmin);
  }
  
  
  .container {
    display: flex;                
    justify-content: space-between; 
    align-items: flex-start;      
    width: 100%;
    height:55vh;            
  }
  
  .title{
    width: 80%;
    box-sizing: border-box;
    position: relative;
    display: inline-block;
    padding-top: 5%; 
    padding-left: 10%;
  }
  
  .profile-add {
    width: 64px;
    height: 64px;
  } 
  
  .medinexus {
    color: #16423c;;
  }
  
  .tagline {
    color: #16423c;;
    font-weight: lighter;
    font-style: oblique;
  }
  
  .login-button {
    background-color: #6a9c89;
    color:#16423c;
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 10px;
    height: 40px;
    width: 150px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .logout-button {
    background-color: #6a9c89;
    color:#16423c;
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 10px;
    height: 40px;
    width: 150px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .login-button:hover {
    background-color: #588272; /* Darker green on hover */
  }
  
  .login-button:focus {
    outline: none; /* Removes outline when button is focused */
  }
  
  .login-button:active {
    background-color: #426256; /* Even darker green when active */
  }
  
  .stock {
    display: flex;
  }
  
  .stock1 {
    width: 60vw;
    height: 55vh;
  }
  
  .stock2 {
    width: auto;
    height: 300px;
  }
  
  .contact-us {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #16423c;
    width: 100%;
    height: 35vh; 
  }
  
  .socials {
    display: inline-block;
  }
  
  .instagram, .facebook, .twitter {
    padding: 20px;
    width: 40px;
    height: 40px;
  }
  
  .newsletter {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  
  .subscribe-text {
    color: #e3e6e0;
    font-style: italic;
    text-align: center;
  }
  
  .input-generate {
    width: 100%;
    padding-top: 15px;
    display: flex;
    justify-content: center;
  }
  
  .email-input {
    width: 30%;
  }
  
  .newsletter-subscribe {
    background-color: #e3e6e0;
    color: #16423c;
    cursor: pointer;
    height: 80%;
    border-radius: 6px;
  }
  .newsletter-subscribe:hover {
    background-color: #16423c;
    color: #e3e6e0;
  }
  