.doctor-sign-up{
    height: 90vh;
    width: 100vw;
    font-family: Arial, sans-serif;
    background-color: #fff; /* Dark green background */
    display: flex;
  }
  
  .sign-up-container {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 10px;
  }

  .user-type {
    display: flex;
    gap: 50px;
  }

  .doctor, .patient {
    font-size: 16px;
    color: #16423c;
    margin: 10px;
    border: 2px solid #16423c;
    padding: 10px;
    background-color: #6a9c89b5;
    width: 15vw;
  }
  
  .doctor:hover, .patient:hover {
    background-color: #6a9c89;
    cursor: pointer;
  }

  .doctor.active, .patient.active {
    background-color: #16423c;
    color: #e3e6e0;
  }

  h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
    color: #16423c;
  }
  
  form {
    text-align: center;
    width: 60%;
  }
  
  .name, .email, .password, .set-password {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border-radius: 5px;
    font-size: 16px;
    background-color: #b0b0b064;
  }
  
  .signup {
    background: content-box;
    border: 2px;
    color: #16423c;
    border-radius: 5px;
    cursor: pointer;
    font-size: 20px;
    border: 2px solid #16423c;
    transition: background-color 0.2s ease-in-out;
    width: 50%;
    margin: 20px;
  }
  
  .signup:hover {
    background-color: #16423c;
    color: #e3e6e0;
  }

  .alt-sign-up-opts {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border: 2px solid #16423c;
    background: #b0b0b064;
  }
  .text {
    color: #16423c;
    padding: 10px;
  }

  .or {
    color: #16423c;
    padding: 30px;
  }

  .google-icon {
    width: 25px;
    height: 25x;
    padding: 10px;
  }

  .forgot_password {
    padding: 10px;
    color: #6f6f6f;
    font-size: 14px;
  }

  .sign-up-content {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #16423c;
    color: #e3e6e0;
    border-top-right-radius: 80px;
    border-bottom-right-radius: 80px;
  }

  .welcome {
    color: #e3e6e0;
  }

  .para {
    padding: 10px;
    text-align: center;
  }

  .sign-in {
    font-size: 20px;
    margin: 10px;
    background: content-box;
    border-width: 2px;
    color: #e3e6e0;
    border-radius: 5px;
    width: 50%;
    margin: 10px;
  }

  .sign-in:hover {
    background-color: #e3e6e0;
    color: #16423c;
    cursor: pointer;
  }

  .fade-enter {
    opacity: 0;
  }
  
  .fade-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
  }
  
  .fade-exit {
    opacity: 1;
  }
  
  .fade-exit-active {
    opacity: 0;
    transition: opacity 300ms ease-in;
  }
  